.login{
background-color: white;
height: 100vh;
display: flex;
flex-direction: column;
align-items: center;
}


.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray ;
}

.login__logo{
    width:100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;

}
.login__container >h1{
font-weight: 500;
margin-bottom: 20px;
}

.login__container > form > h5 {
margin-bottom: 5px;
}

.login__container > form >input{
    height: 30px;
    margin-bottom: 10px;
    background-color:white;
    width: 98%;
}

.login__container > p{
    font-size: 12px;
}

.login_signInButton{
    background:#f0c14b ;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border:1px solid;
    margin-top: 10px;
    background-color: #a88734  #9c7e31 #846a29;
}

login_registerButton{
    background:#f0c14b ;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border:1px solid;
    margin-top: 10px;
    background-color: darkgray;
}